<template>
  <div class="myOrder">
    <div class="navtop">
      <van-nav-bar
        :border="false"
        left-text="我的订单"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>

    <!-- 标签页 -->
    <van-tabs v-model="active" @click="onClick" animated>
      <van-tab v-for="(item, i) in orderStatus" :key="i" :title="item">
        <!-- 订单 -->
        <div class="order" v-for="(val, j) in orderInfo" :key="j">
          <div class="head">
            <span class="tiem">{{ val._add_time }}</span>
            <span class="status">{{ val._status._title }}</span>
          </div>
          <ul class="orderList">
            <Card
              v-for="(obj, k) in val.cartInfo"
              :key="k"
              :orderObj="obj"
            ></Card>
          </ul>
          <p class="price_num">
            共计{{ val.total_num }}件商品<span>￥{{ val.pay_price }}</span>
          </p>
          <div class="btn">
            <div class="cance" @click="cancelOrder(val.order_id)">取消订单</div>
            <div class="payment" @click="payment(val.order_id)">立即付款</div>
          </div>
        </div>
      </van-tab>
    </van-tabs>

    <div class="img" v-if="orderInfo == 0">
      <img src="../assets/image/emptyorder.png" />
      <p class="title">您暂时还没有相关的订单</p>
      <p class="sub">可以以去看看有什么想买的</p>
    </div>

    <!-- 猜你喜欢 -->
    <GoodsList :List="GoodsList"
      ><div class="list_hd">
        <img
          src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png"
        /></div
    ></GoodsList>
  </div>
</template>

<script>
import "../assets/style/order.less";
import Card from "../components/GoodsCard";
import GoodsList from "../components/GoodsList";
export default {
  name: "Order",
  components: {
    Card,
    GoodsList,
  },
  data() {
    return {
      active: 0,

      //订单状态
      orderStatus: ["全部", "待付款", "待发货", "待收货", "待评价"],

      //订单信息
      orderInfo: [],

      //猜你喜欢数据
      GoodsList: [],
    };
  },
  created() {
    this.getOrderinfo();

    // 获取猜你喜欢数据
    this.axios(this.baseUrl + "/groom/list/2?page=1&limit=30").then((data) => {
      this.GoodsList = data.data.data.list;
    });
  },
  methods: {
    //返回上一级
    onClickLeft() {
      this.$router.back();
    },

    // 获取订单数据
    getOrderinfo(i) {
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.axios({
        method: "GET",
        url: this.baseUrl + `/order/list?page=1&limit=20&type=${i==1 ? 0 : i}`,
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          console.log(res);
          if (res.data.status == 410000) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 2000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.status == 410001) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 2000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.status == 200) {
            this.orderInfo = res.data.data;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //获取当前点击的订单数据
    onClick(index) {
      console.log(index);
      this.getOrderinfo(index);
    },

    //取消订单
    cancelOrder(id) {
      this.$dialog
        .confirm({
          title: "提示",
          message: "确定要取消订单吗？",
        })
        .then(() => {
          let tokenString = this.$cookies.get("tokenString");

          if (!tokenString) {
            console.log("浏览器token不存在");
            return this.$router.push({ name: "Login" });
          }

          this.axios({
            method: "POST",
            url: this.baseUrl + "/order/cancel",
            data: {
              id,
            },
            headers: {
              "Authori-zation": "Bearer " + tokenString,
            },
          })
            .then((res) => {
              // console.log(res);

              this.$toast({
                message: res.data.msg,
                forbidClick: true,
                duration: 1000,
              });

              this.getOrderinfo();
            })
            .catch((err) => {
              console.log("err ==> ", err);
            });
        })
        .catch(() => {});
    },

    //立即付款
    payment(id) {
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.axios({
        method: "POST",
        url: this.baseUrl + "/order/pay",
        data: {
          from: "weixinh5",
          paytype: "yue",
          uni: id,
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          console.log(res.data);

          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });
        })
        .catch((err) => {
          console.log("err ==> ", err);
        });
    },
  },
};
</script>